import { Col, Empty, Row } from "antd";
import { useCallback, useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import { TaskSprintManageService } from "../../../../services/taskService";
import { Box, MyBox } from "../../../core/MyBox";
import { MyButton } from "../../../core/MyButton";
import { UIFormatDateVNComponent } from "../../../core/UI_FormatComponent";
import ModifyComponent from "./DetailComponent";
import "../task.css";

export default function IndexComponent() {
    const [isLoading, setIsLoading] = useState(false);
    const [data, setData] = useState([]);
    const [visible, setVisible] = useState(false);
    const [visibleEdit, setVisibleEdit] = useState(false);
    const [keyEdit, setKeyEdit] = useState<string | undefined>(undefined);
    const [sprintChoose, setSprintChoose] = useState<string | undefined>();

    const keyData: string | undefined = useParams().key;

    const loadData = useCallback(() => {
        setIsLoading(true);
        TaskSprintManageService.GetList(keyData, undefined, (data: any) => {
            setData(data.data.data.pagedData);
        }, undefined, () => {
            setIsLoading(false);
        });
    }, [setIsLoading, keyData]);

    useEffect(() => {
        loadData()
    }, [loadData]);

    function handleChooseSprint(key: string | undefined) {
        setSprintChoose(key);
    }

    function RenderItem() {
        if (data.length === 0) {
            return <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} />
        }
        return data.map((data: any, index: number) => {
            return (
                <Col key={index} span={24} onClick={() => {
                    handleChooseSprint(data.key)
                }}>
                    <Box className={sprintChoose === data.key ? "box-choose" : ""}>
                        <p><strong>{data.name} </strong></p>
                        <p>Từ ngày <UIFormatDateVNComponent value={data.dateFrom} /></p>
                        <p>Đến <UIFormatDateVNComponent value={data.dateTo} /></p>
                        <p>Trạng thái: {data.statusText}</p>
                        <MyButton type="warning" value="Chi tiết" onClick={() => {
                            handleClickEditSprint(data.key);
                        }} />
                    </Box>
                </Col >
            )
        })
    }

    function handleClickAddSprint() {
        setVisible(true);
    }

    function handleClickEditSprint(key: string | undefined) {
        setVisibleEdit(true);
        setKeyEdit(key);
    }

    return (
        <MyBox
            title="Quản lý giai đoạn"
            isLoading={isLoading}>
            <MyButton type="primary" value="Thêm mới" onClick={handleClickAddSprint} />
            <Row gutter={12}>
                <Col span={6}>
                    <Box>
                        Công việc mới chưa xử lý
                    </Box>
                </Col>

                <Col span={6}>
                    <Box>
                        Công việc cần làm
                    </Box>
                </Col>

                <Col span={6}>
                    <Box>
                        Công việc sắp hết hạn
                    </Box>
                </Col>

                <Col span={6}>
                    <Box>
                        Vấn đề chưa được giải quyết
                    </Box>
                </Col>

                <Col span={4}>
                    <Row gutter={12}>
                        {RenderItem()}
                    </Row>
                </Col>
                <Col span={20}>
                    <Box>
                        <h3>Danh sách task</h3>
                        <Link to="">Chi tiết</Link>
                    </Box>

                    <Box>
                        <h3>Danh sách issue</h3>
                        <Link to="">Chi tiết</Link>
                    </Box>

                    <Box>
                        <h3>To do list</h3>
                        <Link to={`/tools/tasks/projects/${keyData}/sprint/${sprintChoose}/toDoList`}>
                            <MyButton disabled={sprintChoose === undefined} value="Chi tiết"/>
                        </Link>
                    </Box>

                    <Box>
                        <h3>Tài liệu dự án</h3>
                        <Link to="">Chi tiết</Link>
                    </Box>
                </Col>
            </Row>

            <ModifyComponent
                projectKey={keyData}
                visible={visible}
                setVisible={setVisible}
                isLoading={isLoading}
                setIsLoading={setIsLoading}
                loadData={loadData} />

            <ModifyComponent
                keyData={keyEdit}
                setKeyData={setKeyEdit}
                projectKey={keyData}
                visible={visibleEdit}
                setVisible={setVisibleEdit}
                isLoading={isLoading}
                setIsLoading={setIsLoading}
                loadData={loadData} />
        </MyBox>
    );
}