import { Col, Drawer, Form, Row } from "antd";
import moment from "moment";
import { useCallback, useEffect, useState } from "react";
import Swal from "sweetalert2";
import { Fn } from "../../../../helper/function";
import { TaskSprintManageService } from "../../../../services/taskService";
import { MyFormItem } from "../../../core/Input";
import { MyButton } from "../../../core/MyButton";
import { ProjectStatusComponent } from "../ProjectStatusComponent";

export default function ModifyComponent(props: any) {
    const [name, setName] = useState('');
    const [dateFrom, setDateFrom] = useState(moment());
    const [dateTo, setDateTo] = useState(moment());
    const [status, setStatus] = useState();
    const { setIsLoading } = props;

    let loadData = useCallback(() => {
        if (props.keyData !== undefined) {
            setIsLoading(true);
            TaskSprintManageService.GetDetail(props.projectKey, props.keyData, (data: any) => {
                let row = data.data.data;
                setName(row.name);
                setDateFrom(moment(row.dateFrom));
                setDateTo(moment(row.dateTo));
                setStatus(row.status);
            }, undefined, () => {
                setIsLoading(false);
            })
        }
    }, [props.projectKey, props.keyData, setIsLoading])

    useEffect(() => {
        loadData();
    }, [loadData]);

    function HandleOnClose() {
        props.setVisible(false);
        setName('');
        setDateFrom(moment());
        setDateTo(moment());

        if (props.setKeyData !== undefined) {
            props.setKeyData(undefined);
        }
    }

    function HandleSubmitForm() {
        let body = {
            name: name,
            dateFrom: dateFrom.format("yyyy-MM-DD"),
            dateTo: dateTo.format("yyyy-MM-DD"),
            status: status
        };

        setIsLoading(true);

        if (props.keyData !== undefined) {
            Edit(body);
        } else {
            Add(body);
        }
    }

    function Add(body: any) {
        TaskSprintManageService.Add(props.projectKey, body, (data: any) => {
            Fn.PrintMsgSuccess(data);
            HandleOnClose();
            props.loadData();
        }, () => {
            props.setIsLoading(false)
        });
    }

    function Edit(body: any) {
        TaskSprintManageService.GetDetail(props.projectKey, props.keyData, (data: any) => {
            body = Fn.GetTimestamp(data, body);
            TaskSprintManageService.Edit(props.projectKey, props.keyData, body, (data: any) => {
                Fn.PrintMsgSuccess(data);
                HandleOnClose();
                props.loadData();
            }, () => {
                props.setIsLoading(false)
            });
        }, () => {
            props.setIsLoading(false)
        });
    }

    function handleDeleteSprint() {
        Swal.fire({
            title: "Xác nhận xóa",
            text: "Bạn có muốn xóa sprint này không?",
            showDenyButton: true,
            confirmButtonText: "Có",
            denyButtonText: "Không",
        }).then((result: any) => {
            if (result.isConfirmed) {
                doDeleteSprint()
            }
        })
    }

    function doDeleteSprint() {
        props.setIsLoading(true);
        TaskSprintManageService.GetDetail(props.projectKey, props.keyData, (data: any) => {
            TaskSprintManageService.Delete(props.projectKey, props.keyData, Fn.GetTimestamp(data), (data: any) => {
                Fn.PrintMsgSuccess(data);
                HandleOnClose();
                props.loadData();
            }, () => {
                props.setIsLoading(false);
            });
        }, () => {
            props.setIsLoading(false);
        })
    }

    return (
        <Drawer
            title="Chi tiết"
            open={props.visible}
            onClose={HandleOnClose}>

            <Form layout="vertical" onSubmitCapture={HandleSubmitForm}>
                <MyFormItem.Input
                    label="Tên giai đoạn"
                    value={name}
                    setValue={setName} />

                <MyFormItem.DatePicker
                    label="Ngày bắt đầu"
                    value={dateFrom}
                    setValue={setDateFrom} />

                <MyFormItem.DatePicker
                    label="Ngày kết thúc"
                    value={dateTo}
                    setValue={setDateTo} />

                <ProjectStatusComponent
                    label="Trạng thái"
                    projectKey={props.projectKey}
                    type={2}
                    value={status}
                    setValue={setStatus} />

                <Row gutter={24}>
                    <Col span="12">
                        <MyButton
                            isLoading={props.isLoading}
                            htmlType="submit"
                            type="success"
                            value={props.keyData !== undefined ? "Chỉnh sửa" : "Thêm mới"} />
                    </Col>
                    <Col>
                        <MyButton
                            isLoading={props.isLoading}
                            type="danger"
                            disabled={props.keyData === undefined}
                            onClick={handleDeleteSprint}
                            value="Xóa sprint" />
                    </Col>
                </Row>
            </Form>
        </Drawer>
    );
}